  font-family: pirates;
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

@font-face {
  font-family: skullPirate;
  src: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

a {
  text-decoration: none;
}

body {
  margin: 0;
  padding: 0;
  height: 100vh;
  width: 100vw;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
  background-size: cover;
  backdrop-filter: blur(4px) brightness(0.9);
}

.header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #171717;
  margin-bottom: 1rem;
}

h1 {
  font-family: pirates;
  font-size: 3rem;
  font-weight: 100;
  color: #e5e5e5;
}

.main {
  display: flex;
  justify-content: center;
  align-items: center;
}

/* MENU */
.menu {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 3rem;
  border-radius: 0.2rem;
  background-color: #171717;
  color: #e5e5e5;
  font-family: pirates;
  font-size: 2rem;
}

.logo {
  width: 400px;
}

label {
  align-self: flex-start;
  margin-left: 50px;
}

#player-name {
  margin-top: 0.2rem;
  margin-bottom: 2rem;
  border-radius: 0.2rem;
  padding: 0.5rem;
  border-bottom: 3px solid #e5e5e5;
  color: #e5e5e5;
  font-family: pirates;
  font-size: 1.5rem;
  background: transparent;
}

.play {
  font-size: 3rem;
  color: #b91c1c;
  border-radius: 0.2rem;
  border-bottom: 3px solid transparent;
  transition: 200ms all ease-in-out;
}

.play:hover {
  border-bottom: 3px solid #b91c1c;
}

/* IN GAME */
.game-content {
  display: none;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 70px 1fr;
  column-gap: 4rem;
  row-gap: 1rem;
}

.flag {
  display: flex;
  justify-content: space-around;
  align-items: center;
  gap: 1rem;
  color: #e5e5e5;
  background-color: #171717;
  border-radius: 0.2rem;
}

.names {
  font-family: pirates;
  font-size: 2rem;
}

.skull {
  font-family: skullPirate;
  margin-top: 13px;
  align-self: baseline;
  font-size: 3rem;
}

.board {
  display: grid;
  width: fit-content;
  grid-template-columns: repeat(10, 40px);
  background-color: #fef3c790;
  border: 3px dashed #17171795;
  box-sizing: border-box;
}

.box {
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  border: 2px dashed #17171795;
  width: 40px;
  height: 40px;
  font-family: skullPirate;
  color: #b91c1c;
  font-size: 1.5rem;
}

.box:hover {
  cursor: crosshair;
  background-color: #b91c1c90;
}

.ship {
  background-color: #171717;
}

dialog {
  background-color: #e5e5e5;
  border: none;
  font-family: pirates;
  border-radius: 0.4rem;
}

::backdrop {
  background: #17171780;
}

.gameover {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  justify-content: center;
  background-color: #171717;
  border-radius: 0.2rem;
  padding: 2rem;
}

.gameover img {
  grid-row: 1 / 3;
}

dialog p {
  color: #e5e5e5;
  font-size: 4rem;
  justify-self: center;
}

#rematch {
  grid-column: 2 / 3;
  justify-self: end;
}

/* SHIPS PLACEMENT */
.place-ships {
  display: none;
  grid-template-columns: 250px 1fr;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  font-family: pirates;
}

.ships {
  display: grid;
  grid-template-columns: 100px 100px;
  gap: 1rem;
  border: 4px dashed #17171795;
  padding: 1rem;
  border-radius: 0.2rem;
}

.draggable {
  height: 40px;
  background-color: #171717;
}

#five {
  width: 200px;
  grid-column: 1 / 3;
}

#four {
  width: 160px;
  grid-column: 1 / 3;
}

#three {
  width: 120px;
  grid-column: 1 / 3;
}

#two {
  width: 80px;
}

#one {
  width: 40px;
}

#randomise,
#done {
  justify-self: end;
}

#done {
  display: none;
}
